@import '../Helpers/Helpers.scss';

.not-a-game {
  &__top {
    width: min-content;

    position: relative;
    top: -60px;
    left: 40px;

    @include large() {
      top: -80px;
      left: 15px;
    }
  }

  &__icon {
    font-size: 110px;
    z-index: 1;

    @include large() {
      font-size: 140px;
    }

    &-move {
      z-index: 2;
      font-size: 120px;

      position: absolute;
      top: 17px;
      left: 47px;

      @include large() {
        font-size: 150px;

        top: 22px;
        left: 60px;
      }
    }
  }

  &__message {
    width: 100%;
    max-width: 250px;
    font-family: 'Rubik Pixels', sans-serif;
    color: rgba($color: $red , $alpha: 0.4);
    font-size: 15px;

    position: relative;

    @include medium() {
      font-size: 18px;
    }
  }

  &__attention {
    position: relative;
    top: -40px;
    font-size: 20px;
    color: rgba($color: $red , $alpha: 0.4);
  }

  &__list {
    list-style: none;
    padding: 0;
    margin-top: 30px;

    >* {
      font-family: 'Rubik Pixels', sans-serif;
      color: rgba($color: $red , $alpha: 0.4);
    }
  }

  &__item {
    display: flex;
    gap: 70px;
  }
}
